@import url("https://use.typekit.net/jwe1qdx.css");
@import '../utilities/colors';
@import '../utilities/fonts';
@import '../utilities/breakpoints';

.form-group {
  width: 100%;
}

.panel-body {
  color: $black;
}
#finder_tab_nav a {
  font-family: 'Shackleton' !important;
  font-size: 20px !important;
}

#form-group-at {
  display: none !important;
}

.finder_row, #tab-list {
  max-width: 100%;
}

.panel-footer {
  span, a {
    font-family: 'Raleway';
    font-weight: bold;
  }
}

#tab-list {
  min-height: 550px !important;
}

.panel-heading {
  color: #ffffff;
  background-color: $black !important;
  border-color: $black !important;

  label, span {
    font-family: 'Shackleton' !important;
  }

  small {
    color: $white;
    font-family: 'Raleway';
  }
}

.nav-tabs>li.active>a, .nav-tabs>li.active>a:focus {
  border: none;
  box-shadow: inset 0 -2px 0 $cyan !important;
  color: $cyan !important;
  font-family: 'Shackleton';
  font-size: 20px;
}

.nav-tabs>li>a:hover, .nav-tabs>li>a:focus:hover {
  border: none;
  box-shadow: inset 0 -2px 0 $cyan !important;
  color: $cyan !important;
  font-family: 'Shackleton';
  font-size: 20px;
}

.panel-default, .panel {
  border: 2px solid $black;
  border-radius: 0;
  box-shadow: none;
}

#finder_body {
  select,
  input[type="text"] {
    font-family: 'Raleway', sans-serif;
    font-weight: 500;
    font-size: 16px;
    border: 2px solid #000;
    height: 60px;
    margin: 0 20px 20px;
    border-radius: 0 !important;
    padding: 0 20px !important;
    color: $black;

    &::placeholder,
    &:placeholder-shown,
    option {
      color: $black;
      opacity: 1;
    }
  }

  .panel-footer {
    background: $tan;

    a, span {
      color: $black !important
    }
  }


form {
display: flex;
justify-content: flex-start;
align-items: center;
flex-wrap: wrap;
font-family: 'Shackleton', sans-serif;

br {
  display: none !important;
}

#submitBtn {
  position: relative;
  display: inline-block;
  padding: 20px 30px;
  background: $black;
  color: $white;
  @include font_raleway_bold;
  font-size: 16px;
  transition: .25s ease-in-out;
  margin-bottom: 20px;
  text-transform: lowercase;
  border-top: 4px solid $black;
  border-left: 4px solid $black;
  min-width: 180px;
  text-align: center;
  cursor: pointer;
}
}

fieldset {
border: none !important;
}

legend {
display: none !important;
}

#finder_results {
position: relative;

//&:before {
//  content: '';
//  position: absolute;
//  top: 0px;
//  left: -15px;
//  width: 100%;
//  height: 500px;
//  background: $black;
//  z-index: -1;
//}
#map_canvas {
  height: 500px;
  position: relative;
  border: 2px solid $black !important;
}

#finder_table {
  margin: 0;
  width: 100%;
  border-color: $black;
  border-width: 2px;

  thead {
    th {
      font-family: 'Raleway';
    }
  }

  td {
    border-bottom: 2px solid $black;

    .finder_dba_text {
      font-family: 'Shackleton';
      font-weight: 400;
      font-size: 20px;
    }

    &.finder_address a,
    &.finder_phone a,
    &.finder_miles span,
    &.finder_brands span {
      font-family: 'Raleway';
      font-size: 13px;
      font-weight: 500;
    }

    &.finder_address a,
    &.finder_phone a {
      color: $cyan !important;
    }
  }
}
#finder_disclaimer {
  color: #000;
  font-size: 18px;
  font-style: italic;
  margin: 1em;
  padding: 1em;
  font-family: 'Raleway';
}
}

#startover {
visibility: hidden;
}
}