@mixin font_raleway_med {
  font-family: raleway, sans-serif;
  font-weight: 500;
  font-style: normal;
}

@mixin font_raleway_bold {
  font-family: raleway, sans-serif;
  font-weight: 700;
  font-style: normal;
}

@mixin font_shackleton_reg {
  font-family: shackleton, serif;
  font-weight: 400;
  font-style: normal;
}

@mixin font_roc_grotesk_cn_light {
  font-family: roc-grotesk-condensed, sans-serif;
  font-weight: 300;
  font-style: normal;
}

@mixin font_roc_grotesk_cn_med {
  font-family: roc-grotesk-condensed, sans-serif;
  font-weight: 500;
  font-style: normal;
}

@mixin font_roc_grotesk_cn_bold {
  font-family: roc-grotesk-condensed, sans-serif;
  font-weight: 600;
  font-style: normal;
}

